@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
/* rebeccapurple */
:root{
  --bg: rgb(235,250,250);
  --main-bg: rgb(72,61,139);
  --red:rgb(240, 60, 70);
  --green: rgb(50,140,50);
  --hover: rgba(72,61,139,0.5);
}
::-webkit-scrollbar{
  width: 1em;
}

::-webkit-scrollbar-track{
  background: rgba(0,0,0,0.1);
}

::-webkit-scrollbar-thumb{
  background: rgba(0,0,0,0.5);
  border-radius: 100vw;
  border: .25em solid white;
}
::-webkit-scrollbar-thumb:hover{
  background: rgba(72,61,139,0.5);
  background: var(--hover);
}

::-webkit-scrollbar-thumb:active{
  background: rgb(72,61,139);
  background: var(--main-bg);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

ul, li{
  list-style: none;
}

body{
  background-color: rgb(235,250,250);
  background-color: var(--bg);
  font-family:'Lato', sans-serif;
}

main{
  max-width:100vw;
  margin: 0 auto;
  overflow: hidden;
}



.wrap{
  min-height: 20vh;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.row{
 display: flex;
 align-items: center;
  
  box-shadow: 0 0 5px rgba(0, 0, 0, .2);
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  flex-wrap: wrap;
  background-color: #fff;
  padding: 25px 0;
  margin: 60px auto;
}

.container-item{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  justify-content: center;
}

.container-item h1{
  font-size: clamp(1.2rem, 2.5vw, 1.3rem); 
  font-weight: 400;
}

.container-item i{
  color: var(--main-bg);
}

@media screen and (max-width:700px){
  .container-item p{
    font-size: smaller;
  }
  .container-item{
    max-width: auto;
    margin: 0 auto;
  }
}
.CardContainer{
  min-width: 15rem;
  max-width: 15rem;
  margin:10px 10px;  
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 100ms;
  border-radius: 5px;  
}


.CardContainer:hover{
  box-shadow: inset 0 0 5px 2px rgba(0, 0, 0, 0.2);
}

.CardImg{
  margin: 1rem;
  max-width: 12rem;
  min-height: 11rem;
  object-fit: contain;
}

.CardBody{

  margin: 1rem;
  flex-grow: 1;
}

.CardText{
  color: var(--main-bg);
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  line-height: 1.6rem;
  margin-bottom: 1.5rem;
}

.CardFooter.card-footer{
  font-size: 22px;
}
.CardFooter{
  color:var(--main-bg);
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  line-height: 2.3rem;
}

.CardPriceDiscount{
  color:var(--green);
}

.CardPrice{
  color:var(--red);
  text-decoration: line-through;
}
#ofertas{

}

.ofertas h1{
  text-align: center;
  margin: 60px 0;
  -webkit-text-decoration: underline rgba(0,0,0,0.2);
          text-decoration: underline rgba(0,0,0,0.2);
}

.vermas{
  text-align: center;
  font-size: 1.2em;
  margin: 0px auto 60px;
}

.CarouselItem img{
  width: 100%;
  height: auto;
}
.containerFooter{
  min-height: 5vh;
  border-top: 1px solid var(--main-bg);
  background-color: var(--main-bg);

}
.rowFooter{
  box-shadow: none;
  background-color: white;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

.colFooter{
  text-align: center;
}

.ulFooter{
  display: flex;
  margin: 0 10px;
}

.ulFooter li{
  
  margin: 0 20px;
}

.colFooter p{
margin-top: 20px;
}
.rowIconos{
  width:100%;
  box-shadow: 0 0 1px 2px var(--hover);
  text-align: center;
  
}
.colIconos{
  margin: 0 50px;
  width: 300px;
}

.colIconos p{
  margin:0 auto;
  padding: 10px 0;
  font-size: 0.9em;
}

.colIconos h1{
  max-width: ch;
  line-height: 1;
  font-size: 1.4em;
  margin: 15px auto;
}
.imgIcono{
  width: 100px;
  border: 1px solid var(--main-bg);
  box-shadow: 0 0 5px 2px var(--hover);
}
.containerCategorias{
  margin: 0 auto;
  min-height: 50vh;
  width: 100%;

}
.rowCategorias{
  padding: 5px;
  margin: 0 auto;
  overflow: hidden;  
  height: -webkit-fit-content;  
  height: -moz-fit-content;  
  height: fit-content;
  box-shadow: none;
  border-radius: 5px;
}

.itemCategorias{
  border-radius: 5px;
  margin:5px 5px;
  border: 1px solid var(--main-bg);
  transition: 300ms all;
}

.itemCategorias:hover{
  box-shadow: 0 0 5px 2px var(--hover);
}

.imgCat{
  max-width: 150px;
}

.itemCategorias p{
  font-size: larger;
}
.containerDescubri{
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 30vh;
  
}
.imgDescubri{
  max-width: 20rem;
  object-fit: contain;
  height:200px;
}
#cardDescubriLeft{
  display: flex;
  flex-direction: row;
  min-width:40rem;
  margin-right:30px;
  padding:20px;
}

#cardDescubriRight{
  display: flex;
  flex-direction: row;
  min-width:40rem;
  margin-right:30px;
  padding:20px;
}

.descubriTitulo{
  font-size: 17px;
}
.descubriText{
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  font-size: clamp(1rem,5vw,2rem);
  font-weight: 600;
}

@media (max-width:1000px){
  .containerDescubri{
    flex-direction: column;
    grid-gap:20px;
    gap:20px;
    
  }
  .imgDescubri{
    max-width: 15rem;
  }
  #cardDescubriLeft{
   width:100vw;
  }
  
  #cardDescubriRight{
    width:100vw;
  }
  
  .descubriTitulo{
 
  }
  .descubriText{
  
  }
  
}
.header-wrap{
  min-height: 25px;
  background-color:var(--main-bg);
}

.header-list{
  margin-right:10vw;
  display: flex;
  align-items: center;
  justify-content: right;  
}

.header-list .header-item{
  color: #fff;
  text-decoration: none;
  margin: 0 20px;
}

.header-list .header-item:hover{
  cursor: pointer;
  text-shadow: 0 0 3px rgba(0,0,0,0.8),0 0 5px white;
}

