.navItem{
  color: #fff !important;
}

.toggle{
  color:#fff !important;
}

.navbar-toggler{
 background-color: white;
 outline: solid 3px var(--main-bg);;
}
.ContenedorPrincipal{
  max-width: 100%;
  margin:30px 10vw;
  position: relative;
  overflow: hidden;
}

.ContenedorSlideshow{

  display: flex;
}

.Slide{
  min-width: 100%;
  max-height: 500px;
  overflow: hidden;
  transition: .3s ease all;
  z-index:9;
  position: relative;
  margin-left: 60px 0px;

}
.Slide img{
  width: 100%;
  vertical-align: top;
}

.TextoSlide{
  background: rgba(0,0,0,.4);
  color: #fff;
  width: 100%;
  padding: 10px 60px;
  text-align: center;
  position: absolute;
  bottom: 0;
}

@media screen and (max-width: 700px){
  .TextoSlide{
    position: relative;
    background-color: #000;
  }
}
.Controles{
  position: absolute;
  top:0;
  z-index: 10;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.Boton{
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  width: 50px;
  height: 100%;
  text-align: center;
  position: absolute;
  pointer-events: all;
  transition: .3s ease all;
}

.Boton:hover{
  background-color: rgba(255,255,255,.1);
  
}

